<template>
  <div style="position:relative;">
    <HeaderTop :showBack="showBack" :showTtl="showTtl" :showU="showU"></HeaderTop>
    <div class="code-ipt-box">
      <input type="text" class="conversion-ipt" placeholder="请输入兑换码" v-model="conversionCode">
      <mt-button type="default" size="small" @click="doConversion">兑换</mt-button>
    </div>
  </div>
</template>

<script>
import HeaderTop from "@/components/HeaderTop";
import { getAuthcdkey } from '@/api/lession'
export default {
  name: "conversion",
  data() {
    return {
      showU: false,
      showBack: true,
      showTtl: "课程兑换",
      conversionCode: null
    };
  },
  components: {
    HeaderTop
  },
  mounted() {},
  methods: {
    // 兑换
    async doConversion() {
      if (!this.conversionCode) {
        alert("请输入兑换码");
        return false;
      }

      let ret = await getAuthcdkey({
        cdkey: this.conversionCode
      })

      if (ret && ret.flag) {
        
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.code-ipt-box {
  margin: 30px 20px;
  text-align: center;
  .conversion-ipt {
    width: 100%;
    height: 40px;
    text-indent: 20px;
    border: 1px solid #bbb;
    font-size: 12px;
    color: #666;
    border-radius: 2px;
  }
  .mint-button {
    margin-top: 30px;
  }
}
</style>




